* {
  font-family: 'Nunito', 'Roboto Mono', monospace;
}

:root {
  --color-p: #b39ddb;
  --color-g: #89c4a7;
  --color-y: #d6c17a;
}

li {
  list-style-type: none;
}

#logo text{
  -webkit-user-select: none;
          user-select: none;
  font-family:'Roboto Mono';
  font-size:14px;
}

#logo tspan {
  font-family:'Nunito', monospace;
  font-size: 14px;
}

#logo .star {
  fill:var(--color-y);
}

text .tspan-0 {
  fill:var(--color-p);
}

text .tspan-1 {
  fill:var(--color-g);
}


#logo .broken{
  stroke-dasharray:8;
}

#logo .filled{
  fill:black;
}


#logo .end_marked_arrow{
  marker-end:url(#arrow);
}

#logo .start_marked_arrow{
  marker-start:url(#arrow);
}

#logo .end_marked_diamond{
  marker-end:url(#diamond);
}

#logo .start_marked_diamond{
  marker-start:url(#diamond);
}

#logo .end_marked_circle{
  marker-end:url(#circle);
}

#logo .start_marked_circle{
  marker-start:url(#circle);
}

#logo .end_marked_open_circle{
  marker-end:url(#open_circle);
}

#logo .start_marked_open_circle{
  marker-start:url(#open_circle);
}

#logo .end_marked_big_open_circle{
  marker-end:url(#big_open_circle);
}

#logo .start_marked_big_open_circle{
  marker-start:url(#big_open_circle);
}

.fade {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}


.animate-spin-slow {
  -webkit-animation: spin 10s linear infinite;
          animation: spin 10s linear infinite;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

#svgCont {
  height: 100%;
  width: 100%;
}

#svgCont:hover .animate-spin-slow {
  -webkit-animation: spin-reverse 5s linear infinite;
          animation: spin-reverse 5s linear infinite;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  cursor: pointer;
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes spin-reverse {
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes spin-reverse {
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

.hero .title, 
.hero .nickname,
.hero .subtitle {
  color: #d6c17a;
  text-align: center;
  margin: 0;
  padding: 0;
}
@-webkit-keyframes swing {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  50% { -webkit-transform: rotate(15deg); transform: rotate(15deg); }
  100% { -webkit-transform: rotate(-15deg); transform: rotate(-15deg); }
}
@keyframes swing {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  50% { -webkit-transform: rotate(15deg); transform: rotate(15deg); }
  100% { -webkit-transform: rotate(-15deg); transform: rotate(-15deg); }
}

.device-mockup {
  position: relative;
  display: inline-block;
  width: 80%;
  height: 0;
  padding-bottom: 45%;
  border: 1rem solid #33300000;
  border-radius: 1.5rem;
  overflow: hidden;
}

.device-mockup:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 24px;
  background-color: #ccc;
  border-radius: 1.5rem 1.5rem 0 0;
  opacity: 0.25;
  transition: opacity 0.3s ease-in-out;
}

.device-mockup:hover:before,
.device-mockup:hover .toolbar-button {
  opacity: 1;
}

.device-mockup:hover iframe {
  opacity: 1;
}

.device-mockup iframe {
  position: absolute;
  top: 24px;
  left: 0;
  width: 100%;
  height: calc(100% - 24px);
  border: none;
  opacity: 0.25;
  transition: opacity 0.3s ease-in-out;
}

/* Toolbar buttons */
.device-mockup .toolbar-button {
  position: absolute;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  top: 6px;
  opacity: 0.25;
  transition: opacity 0.3s ease-in-out;
}

.device-mockup .toolbar-button.red {
  background-color: #ff5f56;
  left: 8px;
}

.device-mockup .toolbar-button.yellow {
  background-color: #ffbd2e;
  left: 28px;
}

.device-mockup .toolbar-button.green {
  background-color: #27c93f;
  left: 48px;
}

.project-name {
  position: absolute;
  bottom: 3.5rem;
  left: 1rem;
  font-size: 1.2rem;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0.3rem;
  border-radius: 0.6rem;
}

.project-description {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  font-size: 1rem;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0.3rem;
  border-radius: 0.6rem;
}

.github-link {
  position: absolute;
  bottom: 1rem;
  right: 2rem;
  font-size: 1rem;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0.3rem;
  border-radius: 0.6rem;
  text-decoration: none;
}

.github-link:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

html {
  background-color: #0F0F0F;
}

ul {
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

